:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
}

.img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: 15px;
  padding: 22.5px 0;
  font-size: 1em;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__list .nav__item:not(:first-child) {
  margin-left: 20px;
}

